import { Component } from 'react';
import { AntDesign, Ionicons, MaterialCommunityIcons, Entypo, MaterialIcons } from '@expo/vector-icons';
import { StyleSheet, View, TouchableOpacity, Text, Image, FlatList, ScrollView, Linking } from 'react-native';
import theme from '../assets/theme';
import entry from '../assets/entry';
import moment from 'moment';
import StartHeader from './StartHeader';
import AsyncStorage from '@react-native-async-storage/async-storage';

export default class Start extends Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  async componentDidMount() {
    const { navigation, route } = this.props;
    this.sub = navigation.addListener(
      'focus',
      this.didFocus.bind(this)
    );
    // to error
    if (!route.params?.e) {
      navigation.navigate('Error');
      return;
    }
    // prepare session
    let session = await AsyncStorage.getItem('session');
    if (session) session = JSON.parse(session);
    AsyncStorage.setItem('session', JSON.stringify({
      e: route.params.e,
      t: route.params.t,
      x: moment(),
      c: session?.c || [],
      o: session?.o || [],
      v: session?.v,
    }))
    this.setState({
      session: {
        e: route.params.e,
        t: route.params.t,
        x: moment(),
        c: session?.c || [],
        o: session?.o || [],
        v: session?.v,
      }
    })

    // prepare masters
    this.doGet();
  }

  async didFocus() {
  }

  componentWillUnmount() {
    this.sub();
  }

  doGet() {
    const { route } = this.props;

    let serviceEntry = entry[route.params.e].serviceEntry;
    let promiseArray = [];
    let url;
    let params;
    let promiseObject;

    url = serviceEntry + 'api/foldergrps';
    params = new URLSearchParams();
    params.append('orgId', entry[route.params.e]?.orgId);
    params.append('shopId', entry[route.params.e]?.shopId);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'foldergrps' };
    promiseArray.push(promiseObject);

    url = serviceEntry + 'api/folders';
    params = new URLSearchParams();
    params.append('orgId', entry[route.params.e]?.orgId);
    params.append('shopId', entry[route.params.e]?.shopId);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'folders' };
    promiseArray.push(promiseObject);

    url = serviceEntry + 'api/menus';
    params = new URLSearchParams();
    params.append('orgId', entry[route.params.e]?.orgId);
    params.append('shopId', entry[route.params.e]?.shopId);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'menus' };
    promiseArray.push(promiseObject);

    url = serviceEntry + 'api/mods';
    params = new URLSearchParams();
    params.append('orgId', entry[route.params.e]?.orgId);
    params.append('shopId', entry[route.params.e]?.shopId);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'mods' };
    promiseArray.push(promiseObject);

    url = serviceEntry + 'api/mod-bundles';
    params = new URLSearchParams();
    params.append('orgId', entry[route.params.e]?.orgId);
    params.append('shopId', entry[route.params.e]?.shopId);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'modBundles' };
    promiseArray.push(promiseObject);

    url = serviceEntry + 'api/payment-methods';
    params = new URLSearchParams();
    params.append('orgId', entry[route.params.e]?.orgId);
    params.append('shopId', entry[route.params.e]?.shopId);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'paymentMethods' };
    promiseArray.push(promiseObject);

    url = serviceEntry + 'api/settings';
    params = new URLSearchParams();
    params.append('appCode', 'RESTMENU');
    params.append('orgId', entry[route.params.e]?.orgId);
    params.append('shopId', entry[route.params.e]?.shopId);
    url += ('?' + params);
    promiseObject = { url: url, nameString: 'settings' };
    promiseArray.push(promiseObject);

    Promise.all([
      ...promiseArray.map(el =>
        fetch(el.url)
          .then(response => response.json())
          .then(response => {
            console.log('fetching', el.nameString, response);
            return ({ [el.nameString]: response });
          })
      )
    ])
      .then(response => {
        const masters = response.reduce((o, item) => ({ ...o, [Object.keys(item)[0]]: Object.values(item)[0] }), {});
        console.log('masters', masters);
        AsyncStorage.setItem('masters', JSON.stringify(masters));
      })
  }

  render() {
    const { navigation, route, } = this.props;
    const { session } = this.state;

    return (
      <View style={{ flex: 1, backgroundColor: 'white', }}>
        <StartHeader navigation={navigation} session={session} />
        <View style={{ position: 'absolute', top: 80, left: 0, right: 0, bottom: 100 }}>
          <Image
            style={{ flex: 1 }}
            source={{
              uri: entry[route.params?.e]?.wall
            }}
            resizeMode='cover'
          />
        </View>
        {
          route.params?.t ?
            <View style={{ position: 'absolute', left: 0, right: 0, bottom: 120, height: 60, alignItems: 'center', justifyContent: 'center' }}>
              <View style={{ width: 60, height: 60, backgroundColor: 'white', alignItems: 'center', justifyContent: 'center' }}>
                <Text style={{ fontFamily: 'demi', fontSize: 10, letterSpacing: 1, color: theme.grey1 }}>TABLE</Text>
                <Text style={{ fontFamily: 'demi', fontSize: 20, letterSpacing: 1, color: theme.grey1 }}>{route.params.t}</Text>
              </View>
            </View>
            :
            <View style={{ position: 'absolute', left: 0, right: 0, bottom: 120, height: 60, alignItems: 'center', justifyContent: 'center' }}>
              <View style={{ width: 60, height: 60, backgroundColor: 'white', alignItems: 'center', justifyContent: 'center' }}>
                <Text style={{ fontFamily: 'demi', fontSize: 10, letterSpacing: 1, color: theme.grey1 }}>TAKE</Text>
                <Text style={{ fontFamily: 'demi', fontSize: 10, letterSpacing: 1, color: theme.grey1 }}>AWAY</Text>
              </View>
            </View>
        }

        <View style={{ position: 'absolute', height: 100, left: 0, right: 0, bottom: 0, flexDirection: 'row' }}>
          <TouchableOpacity style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}
            onPress={() => navigation.navigate('Login')}>
            <Text style={{ fontFamily: 'demi', fontSize: 14, letterSpacing: 2, color: theme.grey1 }}>START AS</Text>
            <Text style={{ fontFamily: 'demi', fontSize: 20, letterSpacing: 2, color: theme.grey1 }}>MEMBER</Text>
          </TouchableOpacity>
          <View style={{ marginVertical: 20, width: 2, backgroundColor: theme.grey9 }}></View>
          <TouchableOpacity style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}
            onPress={() => navigation.navigate('Menu')}>
            <Text style={{ fontFamily: 'demi', fontSize: 14, letterSpacing: 2, color: theme.grey1 }}>START AS</Text>
            <Text style={{ fontFamily: 'demi', fontSize: 20, letterSpacing: 2, color: theme.grey1 }}>GUEST</Text></TouchableOpacity>
        </View>
      </View>
    );
  }

}

const styles = StyleSheet.create({
});
