import { Component } from 'react';
import { AntDesign, Entypo, MaterialCommunityIcons } from '@expo/vector-icons';
import { StyleSheet, View, SectionList, Animated, TouchableOpacity, Text, Image, TextInput, Alert, ScrollView, Platform } from 'react-native';
import theme from '../assets/theme';
import entry from '../assets/entry';
import moment from 'moment';
import AsyncStorage from '@react-native-async-storage/async-storage';
import MenuFolder from './MenuFolder';
import StartHeader from './StartHeader';

export default class Menu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      maps: [],
      limiteds: [],
    };

    this.handlePressFolder = this.handlePressFolder.bind(this);
    this.handleViewableChanged = this.handleViewableChanged.bind(this);
  }

  async componentDidMount() {
    const masters = await AsyncStorage.getItem('masters');
    const session = await AsyncStorage.getItem('session');
    this.doPrepareMasters(JSON.parse(masters))
    this.setState({
      masters: JSON.parse(masters),
      session: JSON.parse(session),
    }, () => this.doGet())
    const { navigation } = this.props;
    this.sub = navigation.addListener(
      'focus',
      this.didFocus.bind(this)
    );
  }

  async didFocus() {
    const session = await AsyncStorage.getItem('session');
    this.setState({
      session: JSON.parse(session),
    }, () => this.doGet())
  }

  componentWillUnmount() {
    this.sub();
  }

  doPrepareMasters(masters) {
    let maps = masters.folders;
    maps.forEach(el => {
      el.data = masters.menus.filter(item => item.folderId === el.folderId && item.foldergrpId === el.foldergrpId)
    });
    this.setState({
      maps
    })
  }

  doGet() {
    const { navigation, route, } = this.props;
    const { session } = this.state;

    let serviceEntry = entry[session?.e]?.serviceEntry;
    let url = serviceEntry + 'api/restmenu-serve';
    let params = new URLSearchParams();
    params.append('orgId', entry[session.e]?.orgId);
    params.append('shopId', entry[session.e]?.shopId);
    url += ('?' + params);

    fetch(url)
      .then(response => response.json())
      .then(response => {
        this.setState({
          limiteds: response,
        })
      })
  }

  handlePressFolder(index) {
    // console.log('here', index)
    this.sectionListRef.scrollToLocation({
      animated: false,
      sectionIndex: index,
      itemIndex: 0,
    })
  }

  handleViewableChanged({ viewableItems, changed }) {
    // console.log('parent', viewableItems, changed)
    // problematic
    
    this.menuFolderRef.handleViewableChanged({ viewableItems, changed });
  }

  doTranslateDisable(item) {
    const { limiteds } = this.state;
    const found = limiteds?.find(el => el.restmenuRecKey === item.recKey);
    // not within available hours
    if (!moment(new Date(), 'HH:mm:ss').isBetween(moment(item.startTime, 'HH:mm:ss'), moment(item.endTime, 'HH:mm:ss'))) {
      return true;
    }
    // not allowed by staff
    if (found?.statusFlg === 'B' || found?.onlineStatusFlg === 'B') {
      return true;
    }
    return false;
  }

  render() {
    const { navigation, route } = this.props;
    const { masters, maps, session } = this.state;

    return (
      <View style={{ flex: 1, backgroundColor: 'white', }}>
        <StartHeader navigation={navigation}  session={session}/>
        <MenuFolder
          ref={ref => this.menuFolderRef = ref}
          folders={this.state.masters?.folders}
          handlePressFolder={this.handlePressFolder}
        />
        <SectionList
          // style={{ flex: 1 }}
          ref={ref => this.sectionListRef = ref}
          // scrollEnabled={false}
          onViewableItemsChanged={this.handleViewableChanged}
          scrollEventThrottle={10}
          showsVerticalScrollIndicator={false}
          // stickySectionHeadersEnabled
          sections={maps}
          // ListFooterComponent={}
          keyExtractor={(item, index) => index}
          renderItem={({ item, index }) =>
            <View style={{ opacity: this.doTranslateDisable(item) ? 0.5 : 1, flexDirection: 'row', padding: 10, borderBottomColor: theme.grey9, borderBottomWidth: 1, backgroundColor: 'white' }}>
              {
                // translateImageUrl(item.menuId) &&
                <Image
                  style={{ height: 80, width: 80, borderRadius: 5, marginRight: 10, }}
                  source={{
                    uri: entry[session.e]?.wall
                  }}
                  resizeMode='cover'
                />
              }
              <TouchableOpacity style={{ flex: 1 }}
                disabled={this.doTranslateDisable(item)}
                onPress={() =>
                  navigation.navigate('Item', {
                    m: item.menuId,
                  })
                }>
                <View style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
                  <Text style={{ fontFamily: 'demi', fontSize: 18, color: theme.grey1 }}>{item.menuName}</Text>
                </View>
                <Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.grey1 }}>{item.menuNameLang}</Text>
                <View style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
                  <Text style={{ fontFamily: 'demi', fontSize: 16, marginTop: 10, color: theme.grey1 }}>{item.listPrice.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}   </Text>
                  {/* <Text style={{ fontFamily: 'demi', fontSize: 16, marginTop: 10, color: theme.grey1 }}>2 </Text>
                  <View style={{ marginTop: 10, height: 20, width: 20, backgroundColor: theme.reserved, borderRadius: 10 }}></View> */}

                </View>
              </TouchableOpacity>
              {/* <Image
                style={{ position: 'absolute', top: 10, left: 10, height: 80, width: 80, borderRadius: 5, marginRight: 10, }}
                source={{
                  // uri: translateImageUrl(item.menuId)
                  uri: 'https://scontent.fsin14-1.fna.fbcdn.net/v/t1.6435-9/51771413_1986274018108621_8189578584143167488_n.jpg?_nc_cat=109&ccb=1-7&_nc_sid=730e14&_nc_ohc=k8NgqK1aevAAX8ajQXp&_nc_ht=scontent.fsin14-1.fna&oh=00_AfBVtiRktTIuwt_UI4TBVkoiC1dDVSzHUjDdrrLT22eqTA&oe=64CEECE2'
                }}
                resizeMode='cover'
              /> */}
            </View>
          }
          renderSectionHeader={({ section }) => (
            <View style={{ borderBottomWidth: 1, borderBottomColor: theme.grey8, backgroundColor: 'white', }}>
              <Image
                style={{ height: 120, width: '100%', }}
                source={{
                  uri: entry[session.e]?.wall
                }}
                resizeMode='cover'
              />
              <View style={{ padding: 10, paddingTop: 40, paddingBottom: 30 }}>
                <Text style={{ fontFamily: 'demi', fontSize: 20, color: theme.grey1, }}>{section.name}  <Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.grey1 }}>({section.data.length})</Text></Text>
                {
                  (moment(section.startTime, 'HH:mm:ss').format('HH:mm') !== '00:00' || moment(section.endTime, 'HH:mm:ss').format('HH:mm') !== '23:59') ?
                    <Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.grey1, paddingHorizontal: 10 }}><MaterialCommunityIcons name="timer-outline" color="black" /> available {moment(section.startTime, 'HH:mm:ss').format('HH:mm')} ~ {moment(section.endTime, 'HH:mm:ss').format('HH:mm')} only</Text>
                    :
                    <Text style={{ fontFamily: 'regular', fontSize: 16, color: theme.grey1, paddingHorizontal: 10 }}><MaterialCommunityIcons name="timer-outline" color="black" /> available all day</Text>
                }
              </View>
            </View>
          )}
          renderSectionFooter={({ section }) => (
            <View style={{ marginTop: 20, marginBottom: 60 }}><Text style={{ fontFamily: 'regular', fontSize: 10, textAlign: 'center', color: theme.grey5 }}>END</Text></View>
          )}
        />
      </View>
    );
  }

}

const styles = StyleSheet.create({

});
